import React from 'react';

const Profile = () => {
    return (
        <div>
            <Form
                style={{
                    marginBottom: '20px',
                    border: '1px solid #d9d9d9',
                    padding: '20px',
                }}
            >
                <Input addonBefore="닉네임" />
                <Button type="primary">수정</Button>
            </Form>
            <List
                style={{ marginBottom: '20px' }}
                grid={{ gutter: 4, xs: 2, md: 3 }}
            />
        </div>
    );
};

export default Profile;
